@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../fonts/Rubik-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/Rubik-Medium.woff2") format("woff2"), url("../fonts/Rubik-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/Rubik-SemiBold.woff2") format("woff2"), url("../fonts/Rubik-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/Rubik-Bold.woff2") format("woff2"), url("../fonts/Rubik-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  font-family: "Rubik", "Arial", sans-serif;
  color: #414141;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 679px) {
  .container {
    padding: 0 10px;
  }
}

@media (max-width: 679px) {
  .header {
    background-color: #126133;
  }
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
@media (max-width: 679px) {
  .header-top {
    display: none;
  }
}
.header-top__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 170px;
  height: 116px;
  margin: 0 20px 0 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #9c9b95;
}
.header-top__logo svg {
  flex-shrink: 0;
  width: 112px;
  height: 75px;
  margin: 0 0 1px;
  color: #126133;
}
.header-top__list {
  display: flex;
}
@media (max-width: 1023px) {
  .header-top__list {
    flex-direction: column;
  }
}

.header-bottom {
  position: relative;
  display: flex;
  align-items: center;
  padding: 16px 30px;
  border-radius: 16px;
  background: #126133;
}
@media (max-width: 1279px) {
  .header-bottom {
    padding: 20px;
  }
}
@media (max-width: 679px) {
  .header-bottom {
    padding: 6px 0;
    border-radius: 0;
  }
}
.header-bottom.is-open .main-nav__toggle span:nth-child(1) {
  top: 18px;
  transform: rotate(-45deg) translate(0, -50%);
}
.header-bottom.is-open .main-nav__toggle span:nth-child(2) {
  opacity: 0;
  visibility: hidden;
}
.header-bottom.is-open .main-nav__toggle span:nth-child(3) {
  top: 17px;
  transform: rotate(45deg) translate(0, 50%);
}
.header-bottom.is-open .main-nav__menu {
  clip-path: inset(0 0 0 0);
  visibility: visible;
}
.header-bottom__logo {
  z-index: 110;
